import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {selectCells,selectSize,addElem,addCells,selectCounter,getFinish,selectFinishGame,selectTema,selectPlayers} from './counterSlice'
// import { createSlice } from '@reduxjs/toolkit';
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import jus from '../counter/jus/611094158.jpg'
import Darckjus from '../counter/jus/darck_jus.jpg'
import icon from '../counter/jus/pngtree-cartoon-fruit-cute-strawberry-image-png-image_602437.jpg'
import iconDark from './/jus/26258PICmE5waRdJ3pQIA_PIC2018.png_860.png'
// import { CSSTransition,Transition } from 'react-transition-group';
// import ReactCardFlip from 'react-card-flip';
import'./Counter.css'
// import styled from 'styled-components'

export function Counter() {
const [flip,setFlip] = useState(false)
const [time,setTime] = useState(0)
const dispatch = useDispatch();
const cells = useSelector(selectCells)
const players = useSelector(selectPlayers)
const size = useSelector(selectSize);
const counter = useSelector(selectCounter)
const cheked = useSelector(selectTema)
const finichGame = useSelector(selectFinishGame)
  const handleClick = (e) => {
  dispatch(addElem(e.target.id));

  dispatch(getFinish());
  setFlip(!flip)
  }
 useEffect(() =>{
  let timer = setInterval(() =>{
    !finichGame && counter > 0 ? setTime(time + 1) : setTime(0);
  },1000)
    if(finichGame){
    clearInterval(timer)
    }
    
  return () => {
    clearInterval(timer)
  }
 }) 

  return (
    <div className='row' style={{backgroundImage: cheked ?`url(${Darckjus})` : `url(${jus})`}}>

      <div className='menuDiv'>
              <Link to="/logo" className="">
            <button className='menuBtn' >&#10094;&#10094; Menu</button>
              </Link>
        <div className='counter' style={{display: finichGame ? 'none' : 'flex'}} >
      {counter} Step
      </div>
      <div className='counterTime' style={{display: finichGame ? 'none' : 'flex'}} >
      {time} Sec
      </div>
        </div>
        
       <div className='block'  style={{display: finichGame ? 'none' : 'flex'}}>
       {cells.map((item, idx)=>  
        <button 
       style={{width: `${100/size}%`, height: `${100/size*9}px`,backgroundImage: cheked ? `url(${iconDark})`: `url(${icon})` }} 
       className={`btn ${ flip ? 'flip' : ''}`}
       id={idx}
        key={idx}
        onClick={handleClick}
        disabled={item.visible}> 
      { item.visible ? <img  src={item.value} alt="" height='100%' width="100%" ></img>  : ''}
    </button>


      )} 
 </div>

          <div className={finichGame ? 'newBlock' : 'none'}>
          { finichGame ? <div className='counteFinish'>{counter} Step</div> : '' }
          { finichGame ? <div className='counteFinish'>{time} Sec</div> : '' }
          </div>



 <div className='menuDiv'>
        <Link to="/logo/starting" className="" >
      <button className='backBtn' >&#10094; Back</button>
        </Link>
        <div className='playeOne'>
          <div className='block1'><div className={players[0].points > players[1].points ? 'winer' : ''} style={{width:"100px",height:'100%'}}></div></div>
          <div className='block2'>
            <div className='avatar1' style={{margin:"5px 0 0 10px" }}></div><div className='' style={{ color:'#dd507f',fontSize:"40px",textAlign:'center',margin:'20px 0 0 20px'}}>{players[0].name}</div>
          </div>
          <div className='block3'>
         <div style={{fontSize:"50px",margin:"7px 0 0 20px"}}>Points: {players[0].points}</div> 
         <div style={{fontSize:"50px",margin:"7px 0 0 20px"}}>Mount: {players[0].mount}</div> 

          </div>
        </div>



        <div className='playeTwo'>
          <div className='block1'><div className={players[1].points > players[0].points ? 'winer' : ''} style={{width:"100px",height:'100%'}}></div></div>
          <div className='block2'>
            <div className='avatar2' style={{margin:"5px 0 0 10px" }}></div><div className='' style={{ color:'peru',fontSize:"40px",textAlign:'center',margin:'20px 0 0 20px'}}>{players[1].name}</div>
          </div>
          <div className='block3'>
         <div style={{fontSize:"50px",margin:"7px 0 0 20px"}}>Points: {players[1].points}</div> 
         <div style={{fontSize:"50px",margin:"7px 0 0 20px"}}>Mount: {players[1].mount}</div> 

          </div>
        </div>
        </div>
 
</div>
  );
}
